@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&family=Lato:wght@300;400&display=swap);
:root{--primary-color: #17a2b8;--dark-color: #343a40;--light-color: #f4f4f4;--danger-color: #dc3545;--success-color: #28a745}*{box-sizing:border-box;margin:0;padding:0}body{background:linear-gradient(rgba(0, 0, 0, 0), #35284d 100%),#130034 !important;color:#fff}a{color:#17a2b8;color:var(--primary-color);text-decoration:none}ul{list-style:none}.portofolio_img:hover{-webkit-animation:pulse;animation:pulse;-webkit-animation-duration:1s;animation-duration:1s}.portofolio_connect_btn:hover{-webkit-animation:bounce;animation:bounce;-webkit-animation-duration:1s;animation-duration:1s}.nav-item{font-family:"Comic Neue",cursive;font-family:"Lato",sans-serif;font-weight:bold}#basic-navbar-nav{background:linear-gradient(120deg, #8272ff, #0098ff)}.navbar .mint_btn{border-radius:5px;background:#ef6960;font-family:"Comic Neue",serif;font-weight:bold}.mint-control .mint-counter-plus{background:transparent;color:#fff}.mint-control .mint-counter-plus:hover{background:#0098ff}.mint-control .mint-counter-minus{background:transparent;color:#fff}.mint-control .mint-counter-minus:hover{background:#0098ff}.mint-control .mint-counter-input{background:transparent;color:#fff}.mint-control .mint-counter-input:hover{background:#0098ff}.mint-control p{font-size:22px;margin:0px}.mint-control .minted-number span{color:#0098ff !important}.mint-control a{color:#fff !important}.mint-control .btn-counter-group button{background:transparent}.mint-control .btn-counter-group button:hover{background:#0098ff}.mint-control .mint-button button{background:transparent}.mint-control .mint-button button:hover{background:#0098ff}
.gradient {
  background: linear-gradient(120deg, #8272ff, #0098ff);
  color: #ff0000;
}

.page-footer {
  padding-top: 35px;
  border-top: 1px solid #ddd;
  text-align: center;
  padding-bottom: 20px;
}
.nav-link:hover {
  color: #bdbdbd !important;
}
.social-icons {
  color: #313437;
  background-color: #fff;
  text-align: center;
  padding: 70px 0;
}

@media (max-width:767px) {
  .social-icons {
    padding: 50px 0;
  }
}

.social-icons i {
  font-size: 32px;
  display: inline-block;
  color: #757980;
  margin: 0 10px;
  width: 60px;
  height: 60px;
  border: 1px solid #c8ced7;
  text-align: center;
  border-radius: 50%;
  line-height: 60px;
}

.portfolio-bg {
  padding-top: 200px;
  padding-bottom: 200px;
  background: url(/static/media/top-bg.88d75daa.png) top / cover no-repeat;
}
